.main-header{
  background: #481C92;
  color: #ffffff;
}

.navigation-list{
  display: flex;
  justify-content: space-between;
  width: 450px;
  padding-top: 70px;
  margin-left: auto;
  margin-right: 100px;
}

.navigation-item > button{
  color: #ffffff;
  font-size: 18px;
  padding: 5px;
  transition: 600ms;
  &:hover{
    color: #481C92;
    background: #00aaaa;
  }
}

.main-header_greeting{
  font-size: 60px;
  font-weight: normal;
  margin-left: 170px;
  margin-top: 100px;
  margin-bottom: 150px;
  line-height: 100px;
}

.main-header_about{
  background: #ffffff;
  color: #000000;
}

.main-header_social{
  display: flex;
  margin-left: 40px;
  flex-direction: column;
}

.main-header_social > a{
  color: #ffffff;
  margin-bottom: 20px;
  width: 35px;
}

.main-header_social > a > svg{
  width: 30px;
  border-radius: 50%;
  fill: #ffffff;
  transition: 500ms;
  &:hover{
    fill: #00aaaa;
  }
}

@media (max-width: 1140px) {
  .main-header_greeting{
    margin-left: 10%;
  }
}

@media (max-width: 650px) {

  .navigation-list{
    flex-direction: column;
    margin-left: 5%;
  }

  .navigation-item{
    margin-bottom: 20px;
  }

  .main-header_greeting{
    margin-left: 5%;
  }
}